.note {
    background-color: #67d7cc;
    margin-bottom: 10px;
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    white-space: pre-wrap;
}

textarea {
    border: none;
    resize: none;
    background-color: #67d7cc;
}
textarea:focus {
    outline: none;
}